.try-again-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.try-again-container > h1{
    text-decoration: underline;
    margin: 0;
    font-size: 20px;
    margin: 20px;
}

.result-container > p{
    font-size: 20px;
    margin: 7px;
}

.end-buttons{
    position: relative;
    margin: 12px;
    color: #fff;
    border: none;
    border-radius: 4px;
    min-width: 64px;
    padding: 2px 16px;
    vertical-align: middle;
    text-align: center;
    font-size: 18px;
    line-height: 36px;
    cursor: pointer;
    transition: background-color 0.2s ;
}

.start-again-btn{
    background-color: #3e4248;
}

.start-again-btn:hover, .start-again-btn:focus{
    background-color: #808080;
}

.share-btn{
    background-color: #3b5998;
}

.share-btn:hover, .share-btn:focus{
    background-color: #5d7dc4;
}
